<template>
    <div class="container">
        <button  @click="ingilizcedata=false">Türkçe</button> <button @click="ingilizcedata=true">İngilizce</button> <br> <br>
 
        <div class="form-group">
            <select v-model="secilenulke"  class="form-control" @change="ulkeyegoreverigetir()" >
                <option value="" >Global</option>
                 <option v-for="ulke in ulkeler" :key="ulke" :value="ulke.Slug" >{{ulke.Country}}</option>
            </select>
        </div>
            <div class="row justify-content-around">
                <div class="col-md-4">
                    <div class="stats-item btn-info">
                        <span class="item-title" v-if="ingilizcedata">  Confirmed </span>
                        <span class="item-title" v-else>  Vaka Sayısı </span>
                        <span class="item-count">{{vakasayisi.toLocaleString()}}</span>
                    </div>
                </div>
                <div class="col-md-4">
                    <div class="stats-item btn-danger">
                        <span class="item-title" v-if="ingilizcedata">Deaths</span>
                        <span class="item-title" v-else>Ölüm Sayısı</span>
                        <span class="item-count">{{olumsayisi.toLocaleString()}}</span>
                    </div>
                </div>
                <div class="col-md-4">
                    <div class="stats-item btn-success">
                        <span class="item-title" v-if="ingilizcedata">Recovered</span>
                        <span class="item-title" v-else>İyileşen Sayısı</span>
                        <span class="item-count">{{iyilesensayisi.toLocaleString()}}</span>
                    </div>
                </div>
            </div>
                       <table class="table">
        <thead>
        <tr>
           
            <th scope="col" v-if="ingilizcedata">Country</th>
            <th scope="col" v-else>Ülke</th>
            <th scope="col" v-if="ingilizcedata">Confirmed</th>
            <th scope="col" v-else>Vaka Sayısı</th>
            <th scope="col" v-if="ingilizcedata">Deaths</th>
            <th scope="col" v-else>Ölüm Sayısı</th>
            <th scope="col" v-if="ingilizcedata">Recovered</th>
            <th scope="col" v-else>İyileşen Sayısı</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in data.Countries" :key="item">
            <td>{{item.Country}}</td>
            <td>{{item.TotalConfirmed.toLocaleString()}}</td>
               <td>{{item.TotalDeaths.toLocaleString()}}</td>
                  <td>{{item.TotalRecovered.toLocaleString()}}</td>
        </tr>
        </tbody>
    </table>
    </div>
    
            <hr>
    
</template>
          <script>
          import axios from 'axios';
          export default {
              data() {
                  return {
                      ulkeler:{},
                      secilenulke:'',
                      data:{},
                      vakasayisi:0,
                      olumsayisi:0,
                      iyilesensayisi:0,
                      ingilizcedata:true,
                   
                  }
              },
            
              methods:{
                  ulkecek(){
                      axios.get('https://api.covid19api.com/countries')// response tüm verilerdir api'den gelen
                      .then(response=>{this.ulkeler=response.data})
                  },
                  ulkeverilericek(){
                      axios.get('https://api.covid19api.com/summary')
                      .then(response=>{this.vakasayisi=response.data.Global.TotalConfirmed;
                      this.olumsayisi=response.data.Global.TotalDeaths;
                      this.iyilesensayisi=response.data.Global.TotalRecovered;
                      this.data=response.data;})
                  },
                  ulkeyegoreverigetir(){
                      let data = this.data.Countries.filter(country=>{
                          return country.Slug === this.secilenulke;
                      });
                      if(this.secilenulke.length>0){
                      data = data[0];
                      this.vakasayisi=data.TotalConfirmed;
                      this.olumsayisi=data.TotalDeaths;
                      this.iyilesensayisi=data.TotalRecovered;}
                      else{
                             axios.get('https://api.covid19api.com/summary')
                      .then(response=>{this.vakasayisi=response.data.Global.TotalConfirmed;
                      this.olumsayisi=response.data.Global.TotalDeaths;
                      this.iyilesensayisi=response.data.Global.TotalRecovered;
                      this.data=response.data;})
                      }
                     
                  }
              },
              created(){
                 this.ulkecek(); //sayfa yüklendiğinde çalışsın
                 this.ulkeverilericek();
              }
          }
          </script>
 



<style>
    body {
        background: antiquewhite;
    }

    table {
        background: #fff;
    }

    .container {
        margin-top: 40px;
    }

    .stats-item {
        padding: 20px;
        text-align: center;
        margin-bottom: 20px;
    }

    .item-title {
        display: block;
        color: #fff;
        font-size: 17px;
    }

    .item-count {
        font-weight: bold;
        font-size: 30px;
    }
</style>